.headerWrap {
    background-color: #fff;
        display: flex;
        gap: 30px;
        align-items: center;
        min-height: 64px;
        padding-right: 24px;
        padding-left: 24px;
        box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
            0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.navigationLink {
    color: #000;
    text-decoration: none;
    font-size: 25px;
    
}

.active {
    text-decoration: none;
    font-size: 25px;
    font-weight: bold;
    color: #3b18e9;
}

.navigationLink:hover,
.navigationLink:focus {
    color: #8876e2;
}